import {Component, OnInit} from '@angular/core';
import {Router} from '@angular/router';

import {BsModalRef} from 'ngx-bootstrap/modal/bs-modal-ref.service';
import {BsModalService} from 'ngx-bootstrap/modal';
import {SearchAccountService} from '@modules/search-account/shared';
import {Library, Helper, AuthService} from '@core/index';
import {AddComponent} from '../add/add.component';
import {ListsService} from '@modules/lists/shared';

@Component({
  selector: 'app-index',
  templateUrl: './index.component.html',
  styleUrls: ['./index.component.scss']
})
export class IndexComponent implements OnInit {

  dataSource: any;
  selectedRows: Array<any> = [];
  selectedItems: Array<any> = [];
  bsModalRef: BsModalRef;
  statusFilter: any;
  contentSearch: string;
  visibleGetError: false;
  fileName = '';
  fileSize = 0;
  role_branch: boolean;
  listRoleBranchOrigin: Array<any>;

  constructor(
    public authService: AuthService,
    private searchAccountService: SearchAccountService,
    private modalService: BsModalService,
    private router: Router,
    private listService: ListsService
  ) {
    this.statusFilter = [{
      text: 'Hoạt động',
      value: 'HOAT_DONG'
    }, {
      text: 'Không hoạt động',
      value: 'KHONG_HOAT_DONG'
    }];
  }

  ngOnInit() {
    this.loadList();
    this.role_branch = this.listService.getDataConfigLocalStorage();
  }


  loadList(dataParams?) {
    let data;
    if (dataParams) {
      data = dataParams;
    } else {
      data = {
        // username: '',
        // tax_code: Mã số thuế,
        // customer_code: Mã khách hàng,
        // name: Tên đơn vị,
        // status: Trạng thái,
        current_page: 1,
        number_rows_per_page: 1000,
      };
    }

    const sub = this.searchAccountService.postAll(data).subscribe((rs) => {
      if (rs.status === 'success') {
        this.dataSource = rs.data.buyer_accounts;
      } else {
        Library.notify(rs.message, 'error');
      }
    }, err => {
    }, () => {
      sub.unsubscribe();
    });
  }

  selectionChanged(data: any) {
    this.selectedItems = data.selectedRowsData;
  }

  async add() {
    if (this.role_branch) {
      this.listRoleBranchOrigin = await this.listService.handleListRoleBranchUser();
    }
    const initialState = {
      role_branch: this.role_branch,
      roleBranchStatus: this.listRoleBranchOrigin
    };
    this.bsModalRef = this.modalService.show(AddComponent, {
      class: 'modal-lg full-modal', initialState, backdrop: 'static',
      keyboard: false
    });
    const subModal = this.modalService.onHide.subscribe(() => {
      if (this.bsModalRef.content.successSaveItem) {
        this.loadList();
      }
      subModal.unsubscribe();
    });
  }

  async edit() {
    if (this.role_branch) {
      this.listRoleBranchOrigin = await this.listService.handleListRoleBranchUser();
    }
    const initialState = {
      frmData: this.selectedItems[0],
      role_branch: this.role_branch,
      roleBranchStatus: this.listRoleBranchOrigin
    };
    this.bsModalRef = this.modalService.show(AddComponent, {
      class: 'modal-lg full-modal', initialState, backdrop: 'static',
      keyboard: false
    });
    const subModal = this.modalService.onHide.subscribe(() => {
      if (this.bsModalRef.content.successSaveItem) {
        this.loadList();
      }
      subModal.unsubscribe();
    });
  }

  capLaiMk() {
    const _obj = {
      id: this.selectedItems[0].id,
      username: this.selectedItems[0].username,
      user_id_create: this.authService.getCurrentUser().user.id,
      user_name_create: this.authService.getCurrentUser().user.name
    };
    const result = Library.confirm('Bạn có chắc chắn muốn cấp lại mật khẩu cho tài khoản này?', 'Thông báo');
    result.then((dialogResult) => {
      if (dialogResult) {
        Library.showloading();
        this.searchAccountService.capLaiMk(_obj).subscribe(rs => {
          if (rs.status === 'success') {
            Library.notify('Cấp lại mật khẩu thành công', 'success');
            this.loadList();
          } else {
            Library.notify(rs.message, 'error');
          }
        }, err => {
        }, () => {
          Library.hideloading();
        });
      }
    });
  }

  getFileTemp() {
    Library.showloading();
    this.searchAccountService.layFileMau().subscribe(rs => {
      if (rs.status === 'success') {
        Library.notify('Tải file thành công', 'success');
        const f = rs.data.file;
        Helper.dowloadFile(f.name, f.name.split('.').pop(), f.content);
      } else {
        Library.notify(rs.message, 'error');
      }
    }, err => {
    }, () => {
      Library.hideloading();
    });
  }


  search() {
    const data = {
      search_string: this.contentSearch,
      current_page: 1,
      number_rows_per_page: 1000,
    };

    this.loadList(data);
  }

  delete() {
    const ids = [];
    this.selectedItems.forEach(item => {
      const _obj = {
        id: item.id,
        username: item.username,
        user_id_create: this.authService.getCurrentUser().user.id,
        user_name_create: this.authService.getCurrentUser().user.name
      };
      ids.push(_obj);
    });
    const result = Library.confirm('Bạn có chắc chắn muốn xóa đối tượng đã chọn?', 'Thông báo');
    result.then((dialogResult) => {
      if (dialogResult) {
        Library.showloading();
        this.searchAccountService.delete(ids).subscribe(rs => {
          if (rs.status === 'success') {
            Library.notify('Xóa dữ liệu thành công', 'success');
            this.loadList();
          } else {
            Library.notify(rs.message, 'error');
          }
        }, err => {
        }, () => {
          Library.hideloading();
        });
      }
    });
  }

  napDuLieu() {
    document.getElementById('file-bang-ke').click();
  }

  onFileChanged(e) {
    const file = e.target.files[0];
    const extension = file.name.split('.').pop();
    if (extension === 'xlsx') {
      const fileReader = new FileReader();
      fileReader.readAsDataURL(file);
      fileReader.onload = () => {
        const base64result = fileReader.result.toString().split(',')[1];
        Library.showloading();
        this.searchAccountService.napDuLieu(base64result).subscribe(rs => {
          if (rs.status === 'success') {
            if (rs.data.file) {
              Library.notify('Nạp thành công có một số bản ghi bị lỗi', 'warning');
              Helper.dowloadFile(rs.data.file.name, 'xlsx', rs.data.file.content);
              this.loadList();
            } else {
              Library.notify('Nạp thành công', 'success');
              this.loadList();
            }
          } else {
            Library.notify(rs.message, 'error');
          }
        }, err => {
        }, () => {
          Library.hideloading();
        });
      };
    } else {
      Library.notify('Định dạng file không đúng, chỉ nên chọn file có đuôi .xlsx', 'error');
    }
  }

  thayTheToanBo() {

  }

  layBanGhiLoi() {

  }

}
