import {Component, OnInit, ViewChild, AfterViewInit} from '@angular/core';

import {BsModalRef} from 'ngx-bootstrap/modal/bs-modal-ref.service';
import {DxFormComponent} from 'devextreme-angular';
import {Customer, CustomersService} from '@modules/customers/shared';
import {AuthService, Library, VStringService} from '@core/index';
import {SearchAccountService} from '@modules/search-account/shared';

@Component({
  selector: 'app-add',
  templateUrl: './add.component.html',
  styleUrls: ['./add.component.scss']
})
export class AddComponent implements OnInit, AfterViewInit {
  @ViewChild(DxFormComponent) form: DxFormComponent;

  customer_object_codeRadioItems = [
    {
      text: 'Đơn vị tổ chức',
      value: 'DOANH_NGHIEP'
    },
    {
      text: 'Cá nhân',
      value: 'CA_NHAN'
    },
  ];

  frmData = {
    id: '',
    username: '',
    tax_code: '',
    customer_code: '',
    customer_object_code: 'DOANH_NGHIEP',
    name: '',
    password: this.randomString(9, '#a'),
    email: '',
    mobile: '',
    note: '',
    key_code: '',
    address: '',
    status: 'HOAT_DONG',// Trạng thái: HOAT_DONG, KHONG_HOAT_DONG,
    statusDemo: true,
    user_id_create: this.authService.getCurrentUser().user.id,
    user_name_create: this.authService.getCurrentUser().user.name,
    role_branch: null
  };
  _translate: any;
  isValidate = true;
  role_branch = false;
  roleBranchStatus: Array<any>;

  constructor(
    public bsModalRef: BsModalRef,
    public authService: AuthService,
    public searchAccountService: SearchAccountService,
    private customersService: CustomersService,
  ) {
    this._translate = this.authService.getTranslate();
  }

  ngOnInit() {
    if (this.frmData.id) {
      if (this.frmData.status == 'HOAT_DONG') {
        this.frmData.statusDemo = true;
      } else {
        this.frmData.statusDemo = false;
      }
    }
  }

  ngAfterViewInit() {
    if (this.frmData.id) {
      this.radioChange({value: this.frmData.customer_object_code});
    }
  }

  onSubmit(e) {
    // console.log(this.frmData);
    if (VStringService._getValueToString(this.frmData.customer_object_code) == 'DOANH_NGHIEP') {
      this.frmData.key_code = this.frmData.tax_code;
    } else {
      this.frmData.key_code = this.frmData.customer_code;
    }
    let data = JSON.parse(JSON.stringify(this.frmData));
    if (this.frmData.id) {
      this.add(data);
    } else {
      this.add(data);
    }
  }

  add(data) {
    Library.showloading();
    this.searchAccountService.add(data).subscribe(rs => {
      if (rs.status === 'success') {
        Library.notify('Thành công', 'success');
        this.bsModalRef.content.successSaveItem = 'success';
        this.bsModalRef.hide();
      } else {
        Library.notify(rs.message, 'error');
      }
    }, err => {
    }, () => {
      Library.hideloading();
    });
  }

  checkboxChange = (e) => {
    this.checkStatus(e.value);
  };

  radioChange = (e) => {
    if (e.value == 'CA_NHAN') {
      this.isValidate = false;
      this.form.instance.beginUpdate();
      this.form.instance.itemOption('tax_code', {label: {text: 'Mã số thuế'}});
      this.form.instance.itemOption('name', {label: {text: 'Tên khách hàng'}});
      this.form.instance.endUpdate();
    } else {
      this.isValidate = true;
      this.form.instance.beginUpdate();
      this.form.instance.itemOption('tax_code', {label: {text: 'Mã số thuế/Mã ngân sách'}});
      this.form.instance.itemOption('name', {label: {text: 'Tên đơn vị'}});
      this.form.instance.endUpdate();
    }
    // console.log(e)
  };

  checkStatus(v) {
    if (v == true) {
      this.frmData.status = 'HOAT_DONG';
    } else {
      this.frmData.status = 'KHONG_HOAT_DONG';
    }
  }

  randomString(length, chars) {
    var mask = '';
    if (chars.indexOf('a') > -1) {
      mask += 'abcdefghijklmnopqrstuvwxyz';
    }
    if (chars.indexOf('A') > -1) {
      mask += 'ABCDEFGHIJKLMNOPQRSTUVWXYZ';
    }
    if (chars.indexOf('#') > -1) {
      mask += '0123456789';
    }
    if (chars.indexOf('!') > -1) {
      mask += '~`!@#$%^&*()_+-={}[]:";\'<>?,./|\\';
    }
    var result = '';
    for (var i = length; i > 0; --i) {
      result += mask[Math.floor(Math.random() * mask.length)];
    }
    return result;
  }

  onTaxCodeChanged = (e) => {
    if (!this.frmData.id) {
      Library.showloading();
      this.customersService.getCustomerByTaxCode(e.value).subscribe(
        (res: any) => {
          if (res.status === 'success') {
            if (res.data.customers && res.data.customers.length > 0) {
              if (res.data.customers[0].status == 'HOAT_DONG') {
                this.frmData.name = res.data.customers[0].name;
                this.frmData.address = res.data.customers[0].business_address;
                this.frmData.email = res.data.customers[0].email;
                this.frmData.mobile = res.data.customers[0].contact_mobile;
              }
            } else {
              this.getInfoCompany(e.value);
            }
          } else {
            this.getInfoCompany(e.value);
          }
        }, error => Library.notify(error, 'error'),
        () => Library.hideloading()
      );
    }
  };

  getInfoCompany(value) {
    Library.showloading();
    this.customersService.getInfoCompany(value).subscribe(
      (res: any) => {
        if (res.status === 'success') {
          if (res.data.customer.active_status === 1) {
            this.frmData.name = res.data.customer.name;
            this.frmData.address = res.data.customer.business_address;
            this.frmData.mobile = res.data.customer.contact_mobile;
            this.frmData.email = res.data.customer.email;
            //  this.frmData.transaction_address = res.data.customer.transaction_address;
          } else {
            Library.notify(this._translate.NOTIFY.tax_code_inactive, 'error');
          }
        }
      }, error => Library.notify(error, 'error'),
      () => Library.hideloading()
    );
  }

  getChecklistBranch($event) {
    if ($event.length > 0) {
      if ($event.indexOf('TAT_CA') !== -1) {
        const value = $event.filter(item => {
          return item === 'TAT_CA';
        });
        this.frmData.role_branch = value.toString();
      } else {
        this.frmData.role_branch = $event.toString().replace(/,/g, ';');
      }
    } else {
      this.frmData.role_branch = null;
    }
  }

}
