import {NgModule} from '@angular/core';
import {CommonModule} from '@angular/common';
// library
import {devextremeModule} from '@core/index';
// general
import {SharedModule} from '@shared/index';
import {SearchAccountService} from './shared';
import {routing} from './search-account.routing';
import {IndexComponent} from './components/index/index.component';
import {AddComponent} from './components/add/add.component';
import {ListsService} from '@modules/lists/shared';

@NgModule({
  imports: [
    CommonModule,
    routing,
    SharedModule,
    devextremeModule
  ],
  declarations: [
    IndexComponent,
    AddComponent
  ],
  providers: [SearchAccountService, ListsService],
  entryComponents: [AddComponent]
})
export class SearchAccountModule {
}
